import React, { useState } from 'react';
import { Button, Col, Input, Row, Typography } from 'antd';

const { Title, Text } = Typography;

function groupBy(objects, property) {
  // If property is not a function, convert it to a function that accepts one argument (an object) and returns that object's
  // value for property (obj[property])
  if (typeof property !== 'function') {
    const propName = property;
    property = (obj) => obj[propName];
  }

  const groupedObjects = new Map(); // Keys: group names, value: list of items in that group
  for (const object of objects) {
    const groupName = property(object);
    //Make sure that the group exists
    if (!groupedObjects.has(groupName)) {
      groupedObjects.set(groupName, []);
    }
    groupedObjects.get(groupName).push(object);
  }

  // Create an object with the results. Sort the keys so that they are in a sensible "order"
  const result = {};
  for (const key of Array.from(groupedObjects.keys()).sort()) {
    result[key] = groupedObjects.get(key);
  }
  return result;
}

const Index = () => {
  const [word, setWord] = useState('');
  const [outputTitle, setOutputTitle] = useState('');
  const [result, setResult] = useState(null);
  const [synonymResult, setSynonymResult] = useState(null);
  const [loading, setLoading] = useState(false);
  const [mode, setMode] = useState(0);
  const [saved, setSaved] = useState([]);
  const showRhyming = () => {
    setLoading(true);
    setResult([]);
    setOutputTitle(`Words that rhyme with ${word}:`);
    fetch(
      `https://api.datamuse.com/words?rel_rhy=${word}`)
      .then(resp => resp.json())
      .then(data => {
        setLoading(false);
        const ret = groupBy(data, 'numSyllables');
        setResult(ret);
        setMode(1);
      });
  };

  const showSynonym = () => {
    setLoading(true);
    setSynonymResult([]);
    setOutputTitle(`Words with a similar meaning to ${word}:`);
    fetch(
      `https://api.datamuse.com	/words?ml=${word}`)
      .then(resp => resp.json())
      .then(data => {
        setLoading(false);
        setSynonymResult(data);
        setMode(2);
      });
  };

  const showResult = () => {
    switch (mode) {
      case 0:
      default: {
        return (
          <div />
        );
      }
      case 1: {
        return (
          <>
            {
              Object.keys(result).length === 0
                ? <Text>(no results)</Text>
                : Object.keys(result).map(key => {
                  const list = result[key].map(item => {
                    return (
                      <li key={item.word}>
                        <Text>{item.word}</Text>
                        <Button
                          onClick={() => {
                            setSaved(old => [
                              ...old,
                              item.word
                            ]);
                          }}
                        >
                          (save)
                        </Button>
                      </li>
                    );
                  });
                  return (
                    <Row key={key.toString()}>
                      <Col span={24}>
                        <Title level={2}>
                          {`${key} Syllable${key === '1' ? '' : 's'}`}
                        </Title>
                      </Col>
                      <Col span={24}>
                        <ul>
                          {list}
                        </ul>
                      </Col>
                    </Row>
                  );
                })}
          </>
        );
      }
      case 2: {
        return (
          <>
            {
              synonymResult.length === 0
                ? <Text>(no results)</Text>
                : <ul>
                  {synonymResult.map(word => {
                    return (
                      <li key={word.word}>
                        <Text>{word.word}</Text>
                        <Button
                          onClick={() => {
                            setSaved(old => [
                              ...old,
                              word.word
                            ]);
                          }}
                        >
                          (save)
                        </Button>
                      </li>
                    );
                  })}
                </ul>}
          </>
        );
      }
    }
  };

  const savedWords = () => (
    saved.length === 0
      ? <Text>Saved words: (none)</Text>
      : <Text>Saved words: {saved.join(',')}</Text>
  );

  return (
    <div
      onKeyPress={(event) => {
        console.log(event.code);
        if (event.code === 'Enter') {
          showRhyming();
        }
      }}>
      <Row>
        <Title level={1}>Rhyme Finder (579 Problem Set 6)</Title>
      </Row>
      <Row>
        <Text>{savedWords()}</Text>
      </Row>
      <Row>
        <Col span={12}>
          <Input
            value={word}
            onChange={(event) => {
              setWord(event.target.value);
            }}
          />
        </Col>
        <Col>
          <Button
            type={'primary'}
            onClick={showRhyming}
          >
            Show Rhyming Words
          </Button>
        </Col>
        <Col>
          <Button
            onClick={showSynonym}
          >
            Show Synonyms
          </Button>
        </Col>
      </Row>
      <Row>
        <Title level={2}>{outputTitle}</Title>
      </Row>
      {loading
        ? <Text>...loading</Text>
        : showResult()}
    </div>
  );
};

export default Index;

